import {axisBottom, axisLeft} from 'd3-axis'
import {controller, target} from '@github/catalyst'
import {max as d3max, range} from 'd3-array'
import {scaleBand, scaleLinear} from 'd3-scale'
import d3Tip from '../d3/tip'
import {fetchPoll} from '@github-ui/fetch-utils'
import {format} from 'd3-format'
import memoize from '@github/memoize'
import {select} from 'd3-selection'

interface Data {
  count: number
  dependents: number
  label: string
  url: string
  selected: boolean
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
type GraphNetworkData = any

const cachedData = memoize(fetchData)
async function fetchData(url: string): Promise<GraphNetworkData> {
  const response = await fetchPoll(url, {headers: {accept: 'application/json'}})
  return await response.json()
}

@controller
class PackageDependenciesSecurityGraphElement extends HTMLElement {
  @target graph: HTMLElement

  async connectedCallback() {
    const graph = this.graph
    const url = graph.getAttribute('data-url')
    if (!url) {
      return
    }

    // Remove possibly existing cached graph.
    /* eslint-disable-next-line custom-elements/no-dom-traversal-in-connectedcallback */
    for (const el of graph.querySelectorAll('svg')) {
      el.remove()
    }

    graph.classList.add('is-graph-loading')
    graph.classList.remove('is-graph-load-error', 'is-graph-empty')

    try {
      const data = await cachedData(url)
      graph.classList.remove('is-graph-loading')
      if (data != null && data.unusable) {
        graph.classList.add('is-graph-without-usable-data')
      } else if (
        (data != null && data.length === 0) ||
        (data != null && data.summary != null && data.summary.total === 0) ||
        (data[0] != null && data[0].weeks != null && data[0].weeks.length === 0)
      ) {
        graph.classList.add('is-graph-empty')
      } else {
        render(graph, data)
      }
    } catch (error) {
      graph.classList.remove('is-graph-loading')
      graph.classList.add('is-graph-load-error')
      throw error
    }
  }
}

function render(container: Element, data: Data[]) {
  const margin = {
    top: 10,
    right: 0,
    bottom: 20,
    left: 30,
  }

  const width = Math.round(container.getBoundingClientRect().width) - margin.left - margin.right
  const height = Math.round(container.getBoundingClientRect().height) - margin.top - margin.bottom

  const x = scaleBand<number>().domain(range(data.length)).rangeRound([0, width]).padding(0.2)

  const xAxis = axisBottom(x)
    .ticks(data.length)
    .tickFormat(() => '')

  const y = scaleLinear()
    .domain([0, d3max(data, d => d.count)! + 5])
    .range([height, 0])

  const yAxis = axisLeft(y)
    .ticks(3)
    .tickSize(-width)
    .tickFormat(d => format(',~s')(d))

  const vis = select(container)
    .append('svg')
    .attr('width', '100%')
    .attr('height', '100%')
    .attr('viewBox', `0 0 ${width + margin.left + margin.right} ${height + margin.top + margin.bottom}`)
    .attr('preserveAspectRatio', 'xMidYMid meet')
    .append('g')
    .attr('transform', `translate(${margin.left}, ${margin.top})`)

  const tip = d3Tip<Data>()
    .attr('class', 'svg-tip')
    .attr('role', 'status')
    .offset([-10, 0])
    .html(
      d =>
        `${new Intl.NumberFormat('en-US').format(d.count)} ${
          d.count === 1 ? 'vulnerability' : 'vulnerabilities'
        } affecting ${new Intl.NumberFormat('en-US').format(d.dependents)} ${
          d.dependents === 1 ? 'dependent' : 'dependents'
        }`,
    )
  vis.call(tip)

  vis.append('g').attr('class', 'y axis').call(yAxis)

  vis.append('g').attr('class', 'x axis').attr('transform', `translate(0, ${height})`).call(xAxis)

  const bars = vis
    .selectAll('.bar')
    .data(data)
    .enter()
    .append('g')
    .attr('transform', (d, i) => `translate(${x(i)}, 0)`)
    .append('a')
    .attr('class', 'no-underline')
    .attr('aria-label', d => `Filter to ${d.label} severity vulnerabilities`)
    .attr('xlink:href', d => d.url)
    .on('focus', tip.show)
    .on('blur', tip.hide)

  const barRects = bars
    .append('rect')
    .attr('class', d => `alert-label--${d.label.toLowerCase()}`)
    .attr('width', x.bandwidth())
    .attr('height', d => height - y(d.count))
    .attr('y', d => y(d.count))
    .on('mouseover', tip.show)
    .on('mouseout', tip.hide)

  // Dim non-selected severities if a selected severity exists
  if (data.find(d => d.selected)) {
    barRects.attr('style', d => (d.selected ? '' : 'fill-opacity: 0.2'))
  }

  bars
    .append('text')
    .text(d => d.label)
    .attr('class', 'axis')
    .attr('text-anchor', 'middle')
    .attr('y', height + 16)
    .attr('x', x.bandwidth() / 2)
}
